"use client";
import { useState, useEffect } from "react";
import { auth, db } from "@/lib/firebase";
import { doc, getDoc, updateDoc } from "firebase/firestore";
import { updatePassword, reauthenticateWithCredential, EmailAuthProvider, onAuthStateChanged } from "firebase/auth";
import Footer from "@/components/Footer";
import Navbar from "@/components/Navbar";

export default function ManageAccount() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    resume: "",
    currentPassword: "",
    newPassword: "",
  });

  const [userId, setUserId] = useState(null);
  const [user, setUser] = useState(null);
  const [isPasswordSectionOpen, setIsPasswordSectionOpen] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        setUserId(currentUser.uid);

        const userDocRef = doc(db, "users", currentUser.uid);
        const userSnap = await getDoc(userDocRef);
        if (userSnap.exists()) {
          setFormData((prevState) => ({
            ...prevState,
            name: userSnap.data().name,
            email: userSnap.data().email,
          }));
        }
      }
    });

    return () => unsubscribe();
  }, []);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleFileChange = (e) => {
    setFormData({ ...formData, resume: e.target.files[0] });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!userId) return alert("No user logged in.");

    try {
      const userDocRef = doc(db, "users", userId);
      await updateDoc(userDocRef, {
        name: formData.name,
      });
      alert("Account details updated successfully!");
    } catch (error) {
      console.error("Error updating user data:", error);
      alert("Failed to update account.");
    }
  };

  const reauthenticateUser = async () => {
    const credential = EmailAuthProvider.credential(user.email, formData.currentPassword);
    try {
      await reauthenticateWithCredential(user, credential);
      return true;
    } catch (error) {
      alert("Incorrect current password.");
      return false;
    }
  };

  const handlePasswordChange = async (e) => {
    e.preventDefault();
    if (!user) return alert("No user logged in.");

    if (formData.newPassword.length < 6) {
      return alert("New password must be at least 6 characters.");
    }

    try {
      const isReauthenticated = await reauthenticateUser();
      if (!isReauthenticated) return;

      await updatePassword(user, formData.newPassword);
      alert("Password updated successfully!");
      setFormData({ ...formData, currentPassword: "", newPassword: "" });
    } catch (error) {
      console.error("Error updating password:", error);
      alert("Failed to update password.");
    }
  };

  return (
    <>
      <Navbar />
      <div className="min-h-screen bg-gray-100 p-6 mt-16">
        <div className="max-w-4xl mx-auto bg-white shadow-lg rounded-lg p-6">
          <h1 className="text-3xl font-bold text-center text-gray-800">Manage Account</h1>
          <p className="text-center text-gray-500 mt-2">Update your profile, change your password, or delete your account.</p>

          <form onSubmit={handleSubmit} className="mt-6 space-y-4">
            <div>
              <label className="block text-gray-700">Full Name</label>
              <input type="text" name="name" value={formData.name} onChange={handleChange} className="w-full p-3 border rounded-lg" required />
            </div>

            <div>
              <label className="block text-gray-700">Email</label>
              <input type="email" name="email" value={formData.email} className="w-full p-3 border rounded-lg bg-gray-200 cursor-not-allowed" disabled />
            </div>

            <div>
              <label className="block text-gray-700">Upload Resume</label>
              <input type="file" name="resume" onChange={handleFileChange} className="w-full p-2 border rounded-lg" />
            </div>

            <button type="submit" className="w-full bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 transition">
              Update Account
            </button>
          </form>

          <div className="mt-8 p-6 border-t">
            <button
              onClick={() => setIsPasswordSectionOpen(!isPasswordSectionOpen)}
              className="w-full text-left font-semibold text-gray-800 flex justify-between items-center"
            >
              Change Password
              <span>{isPasswordSectionOpen ? "▲" : "▼"}</span>
            </button>
            {isPasswordSectionOpen && (
              <form onSubmit={handlePasswordChange} className="mt-4 space-y-4">
                <div>
                  <label className="block text-gray-700">Current Password</label>
                  <input type="password" name="currentPassword" value={formData.currentPassword} onChange={handleChange} className="w-full p-3 border rounded-lg" required />
                </div>

                <div>
                  <label className="block text-gray-700">New Password</label>
                  <input type="password" name="newPassword" value={formData.newPassword} onChange={handleChange} className="w-full p-3 border rounded-lg" required />
                </div>

                <button type="submit" className="w-full bg-green-500 text-white py-2 px-4 rounded-lg hover:bg-green-600 transition">
                  Change Password
                </button>
              </form>
            )}
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
